import Vue from 'vue';
import Router from 'vue-router';
import Input from './views/Input.vue';
import Simulate from './views/Simulate.vue';
import Edit from './views/Edit.vue';

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      redirect: '/input'
    },
    {
      path: '/input',
      component: Input
    },
    {
      path: '/simulate',
      component: Simulate
    },
    {
      path: '/edit',
      component: Edit
    }
  ]
});
